import {
    Component,
    h,
    Host,
    Prop,
    State,
    Element,
    Event,
    EventEmitter,
} from '@stencil/core';
import { fetchTranslations } from '../../../utils/translation';
import { classlist } from '../../../utils/classlist';

const promptInputContainerVariants = classlist(
    'relative flex flex-col items-end gap-2 rounded-lg border border-solid p-8 transition-[background-color,color,box-shadow] duration-300 ease-in-out',
    {
        variants: {
            invalid: {
                true: 'border-destructive focus-within:border-destructive focus-within:ring-destructive/30 focus-within:ring-2',
                false: 'border-med focus-within:border-active-foreground focus-within:ring-active-foreground/30 focus-within:ring-2',
            },
            disabled: {
                true: 'bg-surface-1 pointer-events-none',
                false: 'bg-white',
            },
        },
        compoundVariants: [
            {
                disabled: true,
                class: 'focus-within:ring-0',
            },
        ],
        defaultVariants: {
            invalid: false,
            disabled: false,
        },
    },
);

const promptInputVariants = classlist(
    'text-body text-dark w-full resize-none border-none bg-transparent break-words whitespace-pre-wrap shadow-none outline-none focus-visible:ring-0 focus-visible:ring-offset-0',
    {
        variants: {
            invalid: {
                true: 'border-destructive-foreground',
                false: 'border-med',
            },
            disabled: {
                true: 'bg-surface-1 pointer-events-none',
                false: 'bg-disableD-LIGHT',
            },
        },
        defaultVariants: {
            invalid: false,
            disabled: false,
        },
    },
);

/**
 * @category Prompt
 * @description A specialized input component optimized for AI prompt-components interfaces. Supports both single-line and multi-line variants with auto-resize, character counting, send/stop functionality, and enhanced UX for conversational interfaces.
 * @slot label -  Custom label content (alternative to using the label prop)
 * @slot actions-left - Custom content displayed at bottom left of input
 * @slot actions-right - Custom content displayed at bottom right of input
 * @slot footer - Custom content displayed below input
 */
@Component({
    tag: 'at-prompt-input',
    shadow: false,
})
export class AtPromptInputComponent {
    /**
     * Label above the input container
     */
    @Prop()
    label: string;

    /**
     * Short description or validation hint if required
     */
    @Prop()
    hint_text: string;

    /**
     * Optional info icon with detailed tooltip description
     */
    @Prop()
    info_text: string;

    /**
     * Error text displayed when invalid is set via max length
     */
    @Prop()
    error_text: string;

    /**
     * Placeholder text to be shown when no input is passed
     */
    @Prop()
    placeholder: string = 'Enter your message...';

    /**
     * Maximum height in pixels for auto-resize
     */
    @Prop()
    max_height: number = 240;

    /**
     * Maximum character length with counter display
     */
    @Prop()
    max_length: number = 2000;

    /**
     * Controls whether the component is in progress (shows stop button) or ready to send (shows send button)
     */
    @Prop()
    in_progress: boolean = false;

    /**
     * The value of the input
     */
    @Prop({ mutable: true })
    value: string = '';

    /**
     * Disable input interactions and apply visual indication
     */
    @Prop()
    disabled?: boolean = false;

    @State() textareaEl: HTMLTextAreaElement;
    @State() translations: any;
    @State() invalid: boolean = false;
    @Element() el: HTMLElement;

    /**
     * Emits when the value changes
     */
    @Event()
    atChange: EventEmitter<string>;

    /**
     * Emits when a message should be sent
     */
    @Event()
    atSubmit: EventEmitter<string>;

    /**
     * Emits when the stop button is clicked
     */
    @Event()
    atStop: EventEmitter<void>;

    /**
     * Emits when the input receives focus
     */
    @Event()
    atFocus: EventEmitter<void>;

    /**
     * @slot label - Custom label content (alternative to using the label prop)
     */

    private inputId = `prompt-input-${Math.random().toString(36).substring(2, 11)}`;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    private focusInput(): void {
        if (this.textareaEl) {
            this.textareaEl.focus();
            this.atFocus.emit();
        }
    }

    handleInput(event: Event): void {
        event.stopPropagation();
        this.value = (event.target as HTMLTextAreaElement).value;

        if (this.max_length && this.value.length > this.max_length) {
            this.invalid = true;
        } else {
            this.invalid = false;
        }

        this.atChange.emit(this.value);
        this.autoResize();
    }

    private autoResize(): void {
        if (this.textareaEl) {
            this.textareaEl.style.height = 'auto';
            const scrollHeight = this.textareaEl.scrollHeight;
            const newHeight = Math.max(
                44,
                Math.min(scrollHeight, this.max_height),
            );
            this.textareaEl.style.height = `${newHeight}px`;
        }
    }

    componentDidLoad(): void {
        setTimeout(() => {
            this.autoResize();
        }, 0);
    }

    handleKeyDown(event: KeyboardEvent): void {
        if (event.key === 'Enter' && !event.shiftKey) {
            event.preventDefault();
            this.sendMessage();
        }
    }

    sendMessage(): void {
        if (this.in_progress) {
            this.atStop.emit();
        } else if (this.value && this.value.trim() !== '' && !this.invalid) {
            this.atSubmit.emit(this.value.trim());
            this.value = '';
            this.invalid = false;
            setTimeout(() => {
                this.autoResize();
                this.focusInput();
            }, 0);
        }
    }

    render() {
        const canSend =
            !this.disabled &&
            !this.invalid &&
            (this.in_progress || (this.value && this.value.trim() !== ''));

        const containerClass = promptInputContainerVariants({
            invalid: this.invalid,
            disabled: this.disabled,
        });

        const inputClass = promptInputVariants({
            invalid: this.invalid,
            disabled: this.disabled,
        });
        return (
            <Host
                id={this.inputId + '-container'}
                class="w-full px-16"
            >
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.info_text) && (
                        <at-form-label
                            label={this.label}
                            for={this.inputId}
                            info_text={this.info_text}
                            data-name="prompt-input-label"
                        />
                    )}

                    {this.hint_text && (
                        <span
                            class="text-light mb-8 inline-block text-xs leading-tight"
                            data-name="prompt-input-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <div class={containerClass}>
                    <textarea
                        class={inputClass}
                        disabled={this.disabled}
                        id={this.inputId}
                        placeholder={this.placeholder}
                        onInput={(event) => this.handleInput(event)}
                        onKeyDown={(event) => this.handleKeyDown(event)}
                        value={this.value}
                        ref={(el) => (this.textareaEl = el)}
                        style={{
                            minHeight: '44px',
                            maxHeight: this.max_height + 'px',
                            overflow: 'auto',
                        }}
                        data-name="prompt-input"
                    />

                    <div class="flex w-full items-center justify-between gap-1">
                        <div class="flex items-center gap-4">
                            <slot name="actions-left" />
                        </div>

                        <div class="flex items-center gap-4 self-end">
                            <slot name="actions-right" />
                            <at-button
                                class="rounded-lg"
                                size="sm"
                                type="secondary"
                                icon={
                                    this.in_progress ? 'stop' : 'arrow_upward'
                                }
                                disabled={!canSend}
                                onClick={() => this.sendMessage()}
                                onKeyDown={(event) => {
                                    if (
                                        event.key === 'Enter' ||
                                        event.key === ' '
                                    ) {
                                        this.sendMessage();
                                    }
                                }}
                                data-name="prompt-send-button"
                            />
                        </div>
                    </div>
                </div>
                <slot name="footer" />
                {this.invalid && (
                    <span
                        class="text-error-dark text-xs"
                        data-name="error-text"
                    >
                        {this.error_text
                            ? this.error_text
                            : this.translations.ATUI.PROMPT.ERROR_MESSAGE}
                    </span>
                )}
            </Host>
        );
    }
}
